@media only screen and (min-width : 1140px) {
  .m-position {
    .p-header {
      .p-col {
        font-size: 14px;
      }
    }

    .p-body {
      .p-row {
        font-size: 14px;
      }
    }
  }
}

.share-position {
  position: absolute;
  top: 0;
  width: 1152px;
  height: 700px;
  z-index: -1;
  background-image: url('../../../../assets/img/share-position-power.svg');

  .my-address {
    position: absolute;
    right: 14px;
    top: 86px;
  }

  .symbol-name,
  .my-pnl {
    font-size: 40px;
    font-weight: 600;
    color: #E0ECFF;
    position: absolute;
    top: 410px;
    left: 100px;
  }

  .my-pnl {
    top: 495px;
  }

  .direction {
    width: 63px;
    height: 22px;
    position: absolute;
    left: 175px;
    top: 384px;
    border-radius: 100px;
    text-align: center;
    line-height: 22px;
    font-size: 14px;
    font-weight: 600;
  }

  .direction.Long {
    border: 1px solid #59AE99;
    color: #59AE99;
  }

  .direction.Short {
    border: 1px solid #CD5E5E;
    color: #CD5E5E;
  }

  .share-title {
    font-size: 52px;
    font-weight: 600;
    color: #FFF;
    position: absolute;
    top: 124px;
    left: 70px;

    .slogan {
      font-weight: 500;
      font-size: 26px;
      color: #F3F4F8;
      opacity: 0.5;
    }
  }
}

.share-position.futures {
  background-image: url('../../../../assets/img/share-position-future.svg');
}

.share-position.options {
  background-image: url('../../../../assets/img/share-position-option.svg');
}

.share-position.power {
  background-image: url('../../../../assets/img/share-position-power.svg');
}

.share-position.options,
.share-position.futures {
  .direction {
    top: 395px;
  }

  .symbol-name,
  .my-pnl {
    left: 105px;
  }

  .symbol-name {
    top: 420px;
  }

  .my-pnl {
    top: 505px;
  }
}



.m-position {
  margin-top: 16px;
  font-size: 14px;
  overflow: auto;
  position: relative;
  white-space: nowrap;
  background-color: #0A172B;
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  height: 242px;
  scrollbar-width: none;

  /* Firefox */
  &::-webkit-scrollbar {
    display: none;
    /* Safari and Chrome */
  }

  .option-liq-price {
    span {
      div {
        display: inline-block;
      }
    }
  }

  .p-header {
    display: flex;
    justify-content: space-between;
    height: 40px;
    align-items: center;
  }

  .p-body {
    font-size: 12px;
    font-weight: 500;

    // position: relative;
    .no-data {
      // margin: auto;
      margin-top: 48px;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-weight: 600;
      font-size: 14px;
      position: absolute;
      left: 44%;
    }
  }

  .p-row {
    display: flex;
    justify-content: space-between;
    color: #E0ECFF;
    align-items: center;
    height: 32px;
    margin-bottom: 8px;
  }

  .p-col.symbol {
    cursor: pointer;
  }

  .p-row.loading-line {
    width: 100%;
    height: 12px;
  }

  .p-col.red {
    color: #CE5E5E;
  }

  .p-col.green {
    color: #59AE99;
  }

  .unrealizedPnl {
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: space-between;
    img {
      margin-left: 4px;
      margin-right: 16px;
    }
  }

  .p-col {
    min-width: 120px;
    margin-right: 16px;

    .red {
      color: #CE5E5E;
    }

    .green {
      color: #59AE99;
    }

    .gray {
      color: #93A1B8;
    }
  }

  .p-col.close-position {
    cursor: pointer;
  }
}